const {add_repr} = require("nv-facutil-basic");
const {
    jobj2tree,
    flatten_to_entries,
    flatten_to_dot_dict,
    get_val_via_pl,
} = require("nvjson");


function _get_val_via_dotk(o,dotk) {
    let pl = dotk.split(".");
    return(get_val_via_pl(pl,o))
}


function _get_dotk(nd) {
    let ances = nd.$ances(true)
    ances.reverse()
    let pl = ances.map(r=>r.key)
    pl=pl.slice(1)
    let dotk = pl.join(".")
    return(dotk)
}

function _sdfs(rt,data) {
    let sdfs = rt.$sdfs()
    sdfs.forEach(
        nd =>  {
            let k = _get_dotk(nd);
            nd.val = data[k]
        }
    )
    return(sdfs)
}


function _set_cfg_via_pl(cfg,pl,v) {
    let tmp = cfg.$$;
    let i;
    for(i=0;i<pl.length-1;i++) {
        tmp = tmp[pl[i]]
    }
    tmp[pl[pl.length-1]]
    tmp[pl[pl.length-1]] = v
}


const SYM_GET_DATA =Symbol("get-data");
const SYM_SET_DATA =Symbol("set-data");


const _handler =  {
    get: function(target,key,receiver) {
        let c;
        if(!target.hasOwnProperty(key)){
            c = new Cfg()
            Reflect.set(target,key,c)
        } else {
            c = Reflect.get(target,key)
        }
        var p = new Proxy(c,_handler)
        return(p)
    },
    set: function (target, key, value, receiver) {
        let c = Reflect.get(target,key);
        c[SYM_SET_DATA](value);
    } 
}




class Cfg {
    #data = null;
    #handler = _handler
    #proxy
    constructor () {
        this.#proxy = new Proxy(this,this.#handler)
    }
    get $$() {return(this.#proxy)} 
    [SYM_SET_DATA](value) {this.#data=value}
    [SYM_GET_DATA]() {return(this.#data)}
    relation() {
        return(JSON.parse(JSON.stringify(this,null,4)))
    }
    data() {
        let dotd = flatten_to_dot_dict(this.relation());
        delete dotd['']
        let ks = Object.keys(dotd)
        ks.forEach(
            k=> {
                let v = _get_val_via_dotk(this,k);
                dotd[k] = v[SYM_GET_DATA]();
            }
        );
        return(dotd)
    }
    json() {
        let relation = this.relation();
        let data = this.data();
        return({relation,data})
    }
    dump() {
        let d = this.json();
        for(let k in d.data) {
            let v = d.data[k]
            if(typeof(v) === "function") {
                d.data[k] = v.toString();
            }
        }
        return(JSON.stringify(d,null,4))
    }
    show() {console.log(this.dump())}
    get [Symbol.toStringTag]() {return(this.#data.toString())}
    tree() {
        let t = jobj2tree(this.relation());
        let rt = t.$fstch();
        let data = this.data();
        let sdfs = _sdfs(rt,data);
        return(sdfs[0])
    }
    sdfs() {
        let t = jobj2tree(this.relation());
        let rt = t.$fstch();
        let data = this.data();
        let sdfs = _sdfs(rt,data);
        return(sdfs)        
    }
}

function repr(that) {return(that.json())}
add_repr(Cfg,repr);



function load_to_sdfs(o) {
    let data = o.data;
    let t = jobj2tree(o.relation);
    let rt = t.$fstch();
    return(_sdfs(rt,data)) 
}

function load(o) {
    let cfg = new Cfg();
    let data = o.data;
    let relation = o.relation;
    let entries = flatten_to_entries(relation);
    let pls = entries.map(e=>JSON.parse(e[0]))
    pls = pls.slice(1)
    pls.forEach(pl=>{
        let v =data[pl.join(".")]
        _set_cfg_via_pl(cfg,pl,v)
    });
    return(cfg)
}

module.exports = {
    Cfg,
    load_to_sdfs,
    load
}
